<template>
  <ion-page>
    <ion-content>
      <ion-header collapse="condense">
        <ion-toolbar>
          <ion-title size="large">Votre jeu : {{gameID}}</ion-title>
        </ion-toolbar>
      </ion-header>
    
      <div class="p-3">
        <div id="container" v-if="error">
          Partie non trouvée
        </div>
        <template v-else>
          <table class="border border-collapse border-gray-200 w-full">
            <thead>
              <tr>
                <th></th>
                <th v-for="player in players" :key="player.id" @click="changeName">{{player.name}}</th>
              </tr>
            </thead>
              <tbody v-for="row in rows" :key="row.order">
                <tr v-for="child in row.children" :key="child.key">
                  <td>{{child.key}}</td>
                  <td v-for="player in players" :key="player.id">
                    <template v-if="canPlay(player, getPlayerScoreFor(child.key, player))">
                      <ion-input placeholder="..." type="number" min="0" max="30" step="1" @click="setAutoValue" @ionChange="changeValueFor" :data-key="child.key" :data-value="child.value" :readonly="Boolean(child.value)" :value="getPlayerScoreFor(child.key, player)" v-if="child.value || child.type === `custom`"></ion-input>
                      <ion-select interface="popover" :data-key="child.key" @ionChange="changeValueFor" :readonly="Boolean(child.value)" :value="getPlayerScoreFor(child.key, player)" v-else>
                        <ion-select-option value=""></ion-select-option>
                        <ion-select-option :value="0">0</ion-select-option>
                        <ion-select-option :value="i*parseInt(child.key)" v-for="i in 5" :key="i">{{i*parseInt(child.key)}}</ion-select-option>
                      </ion-select>
                    </template>
                    <template v-else>
                      {{getPlayerScoreFor(child.key, player)}}
                    </template>
                  </td>
                </tr>
                <tr>
                  <td>T</td>
                  <td v-for="player in players" :key="player.id">
                    {{getTotalForPart(row.order, player, false)}}
                  </td>
                </tr>
                <tr v-if="row.bonus">
                  <td>B</td>
                  <td v-for="player in players" :key="player.id">
                    {{hasBonus(row.order, player) ? row.bonus : 0}}
                  </td>
                </tr>
                <tr v-if="row.bonus">
                  <td>T</td>
                  <td v-for="player in players" :key="player.id">
                    {{getTotalForPart(row.order, player, true)}}
                  </td>
                </tr>
              </tbody>
              <tbody>
                <tr v-for="row in rows" :key="row.order">
                  <td>T{{row.order}}</td>
                  <td v-for="player in players" :key="player.id">{{getTotalForPart(row.order, player, true)}}</td>
                </tr>
                <tr>
                  <td>TG</td>
                  <td v-for="player in players" :key="player.id">{{getGrandTotal(player)}}</td>
                </tr>
              </tbody>
          </table>
        </template>
      </div>
    </ion-content>
    <ion-modal
      :is-open="isOpened"
      css-class="text-center p-5"
    >
      <div class="p-2">
        <span class="text-lg font-bold">Quel est votre nom ?</span>
        <hr>
        <ion-input v-model="name" placeholder="Votre nom"></ion-input>
        <hr>
        <ion-button @click="setName">Confirmer</ion-button>
      </div>
    </ion-modal>
  </ion-page>
</template>

<script lang="ts">
import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonModal, IonInput, IonButton, IonSelect, IonSelectOption } from '@ionic/vue';
import { defineComponent } from 'vue';
import { find, cloneDeep, map, sumBy, toNumber, remove } from 'lodash';

export default defineComponent({
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonModal,
    IonInput,
    IonButton,
    IonSelect,
    IonSelectOption,
  },
  data: () => ({
    playerId: null,
    players: [],
    error: false,
    isOpened: false,
    name: ``,
    rows: [
      {
        order: 1,
        bonus: 35,
        minForBonus: 63,
        children: [
          {
            key: `1`,
            type: `number`,
          },
          {
            key: `2`,
            type: `number`,
          },
          {
            key: `3`,
            type: `number`,
          },
          {
            key: `4`,
            type: `number`,
          },
          {
            key: `5`,
            type: `number`,
          },
          {
            key: `6`,
            type: `number`,
          },
        ]
      },
      {
        order: 2,
        bonus: 0,
        minForBonus: 0,
        children: [
          {
            key: `BR`,
            type: `custom`,
          },
          {
            key: `F`,
            type: `auto`,
            value: 30,
          },
          {
            key: `CA`,
            type: `auto`,
            value: 40,
          },
          {
            key: `PS`,
            type: `auto`,
            value: 25,
          },
          {
            key: `GS`,
            type: `auto`,
            value: 35,
          },
          {
            key: `CH`,
            type: `custom`,
          },
          {
            key: `Y`,
            type: `auto`,
            value: 50,
          },
        ],
      },
      {
        order: 3,
        bonus: 0,
        minForBonus: 0,
        children: [
          {
            key: `+`,
            type: `custom`,
          },
          {
            key: `-`,
            type: `custom`,
          },
        ],
      },
    ],
  }),
  mounted() {
    this.$socket.client.emit(`join`, this.$route.params.id, (error: any) => {
      if (error.notFound) {
        this.error = true;
      }
    });
  },
  computed: {
    gameID(): any {
      return this.$route.params.id;
    },
    canPlay() {
      // score === undefined && 
      return (player: any, score: any): any => player.id === this.playerId;
    },
    currentPlayer(): any {
      return find(this.players as any, { id: this.playerId });
    },
  },
  methods: {
    changeName() {
      this.isOpened = true;
    },
    getGrandTotal(player: any) {
      return sumBy(this.rows, (r: any) => this.getTotalForPart(r.order, player, true));
    },
    hasBonus(order: number, player: any): boolean {
      const part = find(this.rows, { order }) || { bonus: 0, minForBonus: 0 };
      const playerScore = this.getBaseScoreForPart(order, player);

      return playerScore >= part.minForBonus;
    },
    getTotalForPart(order: number, player: any, bonus: boolean) {
      const part = find(this.rows, { order }) || { bonus: 0 };
      const playerScore = this.getBaseScoreForPart(order, player);

      if (bonus && this.hasBonus(order, player)) {
        return playerScore + part.bonus;
      }
      return playerScore;
    },
    getBaseScoreForPart(order: number, player: any) {
      const part = find(this.rows, { order });
      if (!part) return 0;
      const values = map(part.children, `key`);
      let playerScore = sumBy(player.scores, (s: any) => {
        if (values.includes(s.key) && s.key !== `-`) {
          return toNumber(s.value) ||0;
        }
        return 0;
      });

      const minus = find(player.scores, { key: `-` });
      if (find(part.children, { key: `-` })) {
        playerScore -= (minus || {}).value ||0;
      }

      return playerScore;
    },
    setGlobalScore(score: any) {
      this.$socket.client.emit(`setScore`, this.gameID, score);
    },
    saveScore(score: any) {
      localStorage.setItem(`score${this.gameID}`, JSON.stringify(score));
    },
    changeValueFor(event: any) {
      const value = event.detail.value;
      const key = event.target.dataset.key;
      this.setValue(key, value);
    },
    setAutoValue(event: any) {
      const target = event.target.dataset.key ? event.target : event.target.parentElement;
      const key = target.dataset.key;
      let value = target.dataset.value;
      const score = find(this.currentPlayer.scores, { key }) ||{};
      if (score.value === value && value) {
        value = 0;
      } else if (score.value === `0` && value) {
        value = null;
      }
      this.setValue(key, value);
    },
    setValue(key: any, value: any) {
      this.$socket.client.emit(`setValue`, this.gameID, key, value);
      const score = cloneDeep(this.currentPlayer.scores);
      remove(score, { key });
      score.push({
        key,
        value,
      });
      this.saveScore(score);
    },
    getPlayerScoreFor(key: any, player: any) {
      return (find(player.scores, { key }) || {}).value;
    },
    setName() {
      localStorage.setItem('name', this.name);
      this.$socket.client.emit(`setName`, this.gameID, this.name);
      this.isOpened = false;
    },
  },
  sockets: {
    setPlayerId(playerId) {
      this.playerId = playerId;
    },
    setPlayers(players) {
      this.players = players;
    },
    setName() {
      const name = localStorage.getItem(`name`);
      let score = localStorage.getItem(`score${this.gameID}`);
      score = score ? JSON.parse(score) : null;
      console.log(name);
      
      if (name) {
        this.name = name;
        this.setName();
        this.setGlobalScore(score);
      } else {
        this.isOpened = true;
      }
    },
  },
});
</script>

<style scoped>
td {
  @apply text-center border;
}
tr {
  @apply border w-full;
}

th {
  @apply border;
}

</style>
